'use client'
import { useState, useEffect } from 'react';
import Image from 'next/image';
import { Movie } from '@/types';

export default function Home() {
  const [movies, setMovies] = useState<Movie[]>([]);
  const [featuredMovie, setFeaturedMovie] = useState<Movie | null>(null);
  const [loading, setLoading] = useState(true);
  
  const API_KEY = '1ceba15c8e9306a14c1ed7d52a4e4aa0';
  const BASE_IMAGE_URL = 'https://image.tmdb.org/t/p/original';

  useEffect(() => {
    const fetchMovies = async () => {
      try {
        setLoading(true);
        const response = await fetch(
          `https://api.themoviedb.org/3/discover/movie?api_key=${API_KEY}&include_adult=false&include_video=false&language=en-US&page=1&sort_by=popularity.desc`
        );

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        console.log('API Response:', data); // Debug log
        
        if (data.results && data.results.length > 0) {
          setMovies(data.results);
          setFeaturedMovie(data.results[0]);
        }
      } catch (error) {
        console.error('Error fetching movies:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchMovies();
  }, []);

  if (loading) {
    return <div className="h-screen bg-black text-white flex items-center justify-center">Loading...</div>;
  }

  return (
    <div className="min-h-screen bg-black text-white">
      {/* Featured Movie Section */}
      {featuredMovie && (
        <section className="relative h-[85vh] w-full">
          {/* Background Image Wrapper */}
          <div className="absolute inset-0">
            <Image
              src={`${BASE_IMAGE_URL}${featuredMovie.backdrop_path}`}
              alt={featuredMovie.title}
              width={1920}
              height={1080}
              className="w-full h-full object-cover"
              priority
            />
            {/* Gradient overlays */}
            <div className="absolute inset-0 bg-gradient-to-t from-black via-black/60 to-transparent" />
            <div className="absolute inset-0 bg-gradient-to-r from-black/50 via-transparent to-transparent" />
          </div>

          {/* Movie Info */}
          <div className="absolute bottom-32 left-12 z-10 max-w-2xl">
            <h1 className="text-6xl font-bold mb-4">{featuredMovie.title}</h1>
            <p className="text-lg text-gray-200 mb-6">
              {featuredMovie.overview}
            </p>
            <div className="flex items-center gap-4">
              <span className="bg-yellow-400 text-black px-4 py-1.5 rounded-md">
                IMDB: {featuredMovie.vote_average.toFixed(1)}
              </span>
              <span className="bg-white/20 px-4 py-1.5 rounded-md">
                Released On: Jun {new Date(featuredMovie.release_date).getDate()}
              </span>
            </div>
          </div>
        </section>
      )}

      {/* Upcoming Movies Section */}
      <section className="px-12 py-8">
        <h2 className="text-2xl font-bold mb-6">Upcoming Movies</h2>
        <div className="flex space-x-4 overflow-x-auto">
          {movies.slice(0, 9).map((movie) => (
            <div key={movie.id} className="flex-none w-[180px]">
              <div className="relative h-[270px] mb-2 rounded-lg overflow-hidden">
                <Image
                  src={`${BASE_IMAGE_URL}${movie.poster_path}`}
                  alt={movie.title}
                  width={180}
                  height={270}
                  className="w-full h-full object-cover"
                />
                <div className="absolute inset-0 bg-black/20 transition-colors hover:bg-black/40" />
              </div>
              <h3 className="font-medium text-sm truncate">{movie.title}</h3>
              <p className="text-xs text-gray-500">
                {new Date(movie.release_date).getFullYear()}
              </p>
            </div>
          ))}
        </div>
      </section>

      {/* Debug Info */}
      <div className="fixed top-0 left-0 bg-black/50 p-2 text-xs">
        Featured Movie: {featuredMovie?.title}
        <br />
        Total Movies: {movies.length}
      </div>
    </div>
  );
}